import { faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { Link } from 'gatsby'
import React from 'react'
import { BlogBody, PublishDate } from '../../templates/blog-post'
import BannerBlog from '../banner/blog'
import FreeConsultationForm from '../free-consultation-form'
import GoogleMap from '../google-map'
import { IconLink } from '../icon-link-button'
import Layout from '../layout'
import Seo from '../seo'
import BlogWrapper from './blog-wrapper'

function BlogTeaser({
  id,
  title,
  body,
  slug,
  publishDate,
}) {
  const link = `/blog/${slug}`;

  return (
    <article key={`blog-teaser-${id}`} className="space-y-8">
      <Link className="text-[40px] text-primary font-bold" to={link}>{title}</Link>
      <BlogBody data={body.data.body} />
      <IconLink to={link} icon={faChevronRight}>
        view more
      </IconLink>
      <PublishDate date={publishDate} />
    </article>
  )
}

function BlogListPage({
  data,
  seo,
  title = "",
}) {
  return (
    <Layout>
      <Seo seo={seo} />
      { !title ? <BannerBlog /> : '' }
      <BlogWrapper>
        { title ? <h1 className="page-title text-primary font-bold">{title}</h1> : ''}
        { data.map(blogData => <BlogTeaser key={`${blogData.id}`} {...blogData} />)}
      </BlogWrapper>
      <GoogleMap />
      <FreeConsultationForm />
    </Layout>
  )
}

export default BlogListPage