import gsap from 'gsap';
import { TweenMax } from 'gsap/gsap-core';
import React, { useEffect, useRef } from 'react';
import BannerBlogImg from './banner-blog.svg';

const BannerBlog = () => {
  const el = useRef();
  const q = gsap.utils.selector(el);

  useEffect(() => {
    const bubblePopUp = q('.bubble-pop-up')
    
    TweenMax.staggerFrom(bubblePopUp, .2, {transformOrigin: "50% 100%", scale: '0', stagger: 2, repeat: -1, yoyo: true, repeatDelay: true, delay: 2});
  }, [])

  return (
    <section ref={el} className="banner-section lg:h-full max-h-[600px] lg:max-h-none lg:min-h-[600px] flex flex-col">
      <div className="banner-wrapper">
        <h1 className="page-title mr-auto relative p-6 md:p-0">
          our<br />
          <span className="text-primary font-bold">blog</span>
        </h1>
        <div className="banner banner-blog">
          <BannerBlogImg />
        </div>
      </div>
    </section>
  )
}

export default BannerBlog;